

import DeleteButton from '@/components/ui/DeleteButton.vue'
import PreloaderLocal from '@/components/PreloaderLocal.vue'
import OrderDraftCard from '@/components/cards/Order/OrderDraftCard.vue'

import { ref, computed, defineComponent, watch } from 'vue'
import { useStore } from 'vuex'
import { onClickOutside } from '@vueuse/core'

import { key } from '@/store'
import { OrderActions } from '@/store/order/actions'
import { useRouter } from 'vue-router'
import { OrderMutations } from '@/store/order/mutations'


export default defineComponent({
	name: 'OrderDetailModal',
	components:{
		DeleteButton,
		PreloaderLocal,
		OrderDraftCard,
	},
	props:{
		modelValue:{
			type: Boolean
		},
		orderId: {
			type: Number,
			default: -1
		},
		repeatOrder: {
			type: Boolean,
			default: false
		}
	},
	emits: ['update:modelValue', 'update:repeatOrder'],
	setup(props, { emit }){
		const store = useStore(key)
		const router = useRouter()
		const shake = ref(false)
		const targetModal = ref(null)
		const loading = ref(false)
		const useRepeat = ref(false)
		
		onClickOutside(targetModal, () => {
			shake.value = true
			setTimeout(() => {shake.value=false;}, 500)
		});
		const close = () => {
			emit('update:repeatOrder', false)
			emit('update:modelValue', false)
		};
		watch( ()=>props.orderId, ()=>{
			if (props.orderId!=-1){
				loading.value=true
				store.dispatch(OrderActions.GET_ORDER_DETAIL, props.orderId ).finally(()=>{loading.value = false})
			}
		})
		
		
		const  setOrder = async () => {
			await store.commit(OrderMutations.CREATE_ORDER_FROM_DETAIL)
			router.push({name: 'Order'})
		}
		
		return {
			//data
			shake,
			targetModal,
			loading,
			useRepeat,

			//computed
			order_detail: computed(() => store.getters.getOrderDetail),
			order: computed(() => store.getters.getOrdersByID(props.orderId)),

			//methods
			close,
			setOrder,
		}
	}
})
